$md: 1200px;
$sm: 992px;
$xs: 768px;

$shipgrey: #3B3744;
$rum: #756A7C;
$tapa: #79746B;
$alto: #DDDBDA;

@import "css/bootstrap.css";
@import "flex";
@import "spacing";
@import "typography";
@import url('https://fonts.googleapis.com/css2?family=Artifika&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Berkshire+Swash&display=swap');

body {
  line-height: 1.5rem;
  margin: 0;
  font-size: 100%;
  font-family: "Lato", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: scroll;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.artifika {
  font-family: 'Artifika', serif;
}

.berkshire-swash {
  font-family: 'Berkshire Swash', serif;
}

.lato {
  font-family: 'Lato', sans-serif;
}

.width-100 {
  width: 100%;
}

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

button {
  font: unset;
  text-align: unset;
  background-color: unset;
  border: unset;
  cursor: pointer;
}

.width-100 {
  width: 100%;
}

.height-100 {
  height: 100%;
}

.height-full {
  min-height: 100vh;
}

.align-right {
  text-align: right;
}

.pointer {
  cursor: pointer;
}

a.no-link-format {
  color: inherit;
  text-decoration: inherit;
}

.header {
  background-color: $shipgrey;
  line-height: 1.2;
  color: white;
  position: fixed;
  top: 0;
  width: 100%;
  height: 38px;
  z-index: 10;
}

.below-header {
  margin-top: 38px;
}